
import React, { Component } from "react";
import MultiSelect from "@kenshooui/react-multi-select";
import { InputGroup, Button, FormControl, Card, Alert } from "react-bootstrap";
import axios from "axios"
import Aux from "../../../hoc/_Aux";
import "@kenshooui/react-multi-select/dist/style.css"
import { confirmAlert } from "react-confirm-alert";
import "react-confirm-alert/src/react-confirm-alert.css"; 
var urlbackend = require("../../../env/env")();

class ColisConsoleDecision extends Component {
    constructor(props) {
        super(props);
        this.handleChange = this.handleChange.bind(this);
        this.state = {
            items: [],
            selectedItems: [],
            drivers: [],
            iddriver: ''
        };
    }
    componentDidMount() {
        const config = {
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Headers': 'Authorization',
                "x-access-token": localStorage.getItem('token'),
                'Authorization': 'Bearer ' + localStorage.getItem('token'),
            },
        };
        var getPendingPack = axios.get(urlbackend + "/console/for_delivery?current_page=1&per_page=500", config);



        var getdrivers = axios.get(urlbackend + "/driver", config);

        axios
            .all([getPendingPack, getdrivers])
            .then(
                axios.spread((...response) => {
                    console.log("this respon list0", response[0].data);
                    
                     this.setState({ selectedItems:[] });
                    this.setState({ drivers: response[1].data.data });
                    this.setState({ items: Object.values(response[0].data.data.data ).filter(e=>e.check_magasinier_to===1)});

                })
            )
            .catch((errors) => {
                // react on errors.
                console.error(errors);
            });
    }
    //affectation des "driver" par groupe body : {driver:"iddriver" , package:["idpackage", .....]  url "/v3/package/edit/driver" methode "PUT
    savedriver() {
        //
      
        confirmAlert({
            title: 'Confirmer dispatch',
            message: 'confirmer dispatch colis?',
            buttons: [
                {
                    label: 'Yes',
                    onClick: () => {
                        const config = {
                            headers: {
                                'Content-Type': 'application/json',
                                'Access-Control-Allow-Headers': 'Authorization',
                                "x-access-token": localStorage.getItem('token'),
                                'Authorization': 'Bearer ' + localStorage.getItem('token'),
                            },
                        };
                        axios.post(urlbackend + "/mobile/delivery/add", {driver:this.state.iddriver, colis:this.state.selectedItems.map((e) => (e.id))}, config)
                            .then(response => {
                                alert("add Succefull")
                                console.log(response.data)
                                this.componentDidMount();
                            })
                            .catch(e => {
                               alert(e.response.data.message)
                            })
                
                
                    }
                },
                {
                    label: 'No',
                    onClick: null
                }
            ]
        });


       
    }
    handleChange(selectedItems) {
        this.setState({ selectedItems });
    }
    render() {
        const { items, selectedItems } = this.state;
        return (
            <Aux>

                <Button onClick={() => this.savedriver()} > envoyer</Button>

                <Card>
                    <InputGroup className="mb-3">
                        <FormControl
                            as="select"
                            id="{this.state.driver}"
                            onChange={(e) =>
                                this.setState({ iddriver: e.target.value })
                            }
                            custom
                        >
                            <option>Choisir un Chauffeur</option>

                            {this.state.drivers.map((driver, index) => (
                                <option
                                    className=" badge badge-info"
                                    key={driver.id}
                                    value={driver.id}
                                >
                                    {driver.username}
                                </option>
                            ))}
                        </FormControl>
                    </InputGroup>


                    <MultiSelect
                        items={items.map(e => {
                            return { "id": e.colis?.id, label:e.colis?.index_colis + "| "+ e.colis?.name_complete + ":" +e.colis?.address + e.code, address:e.colis?.address ,provider: e?.provider ,group:e.colis?.gouvernorat }

                        })}
                        withGrouping

                        selectedItems={selectedItems}
                        onChange={this.handleChange}
                        responsiveHeight= {800}

                    />
                </Card>
            </Aux>
        );
    }
}
export default ColisConsoleDecision